.main-stars {
    position: absolute;
    left: 0;
    top: 125px;
    width: 100%;
    height: 505px;
    pointer-events: none;
}

.main-screen {
    padding-top: 95px;
}

.me {
    position: absolute;
    left: 53%;
    top: -187px;
    z-index: -1;
    opacity: 0;
    visibility: hidden;
    transition: 0.5s;
}

.nameHover {
    font-weight: bold;
    text-decoration: underline;
    transition: .5s;
    &:hover .me {
        opacity: 1;
        visibility: visible;
    }
}

.main-text {
    max-width: 750px;
    margin: 0 auto;
    padding: 290px 0 45px 0;
    position: relative;
    z-index: 1;
    h1 {
        font-size: 48px;
        line-height: 55px;
        color: #FFFFFF;
        font-weight: normal;
        z-index: 1;
        position: relative;
    }
}

.mountain {
    position: absolute;
    left: 0;
    width: 100%;
    height: 730px;
}

.bulb {
    animation-duration: 1s;
    animation-name: glow;
    animation-iteration-count: infinite;
    animation-timing-function: ease-in-out;
    animation-direction: alternate;
    animation-name: glow;
    animation-duration: 1s;
    animation-iteration-count: infinite;
    animation-timing-function: ease-in-out;
    animation-direction: alternate;
}

@keyframes glow {
    0% {
        fill-opacity: 0;
    }
    100% {
        fill-opacity: 1.0
    }
}

.blink-1 {
    animation-delay: 0.6s;
    animation-delay: 0.6s;
}

.blink-2 {
    animation-delay: 2s;
    animation-delay: 2s;
}

.blink-3 {
    animation-delay: 2s;
    animation-delay: 2s;
}

.blink-4 {
    animation-delay: 6s;
    animation-delay: 6s;
}

.blink-5 {
    animation-delay: 2.5s;
    animation-delay: 2.5s;
}

.blink-6 {
    animation-delay: .3s;
    animation-delay: .3s;
}

.blink-7 {
    animation-delay: 2s;
    animation-delay: 2s;
}

.blink-8 {
    animation-delay: .2s;
    animation-delay: .2s;
}

.blink-9 {
    animation-delay: 0.95s;
    animation-delay: 0.95s;
}

.blink-10 {
    animation-delay: 1.2s;
    animation-delay: 1.2s;
}

#leaves {
    position: fixed;
    top: -50px;
    left: 0;
    width: 100%;
    height: 100vh;
    text-align: right;
    text-align: right;
    pointer-events: none;
}

#leaves i {
    display: inline-block;
    width: 200px;
    height: 150px;
    background: url(../img/leaf.svg) !important;
    background-size: cover !important;
    z-index: 1;
    animation: falling 7s 0s infinite;
}

#leaves i:nth-of-type(2n) {
    animation: falling2 7s 0s infinite;
}

#leaves i:nth-of-type(3n) {
    animation: falling3 7s 0s infinite;
}

#leaves i:before {
    position: absolute;
    content: '';
    top: 117px;
    right: 9px;
    height: 27px;
    width: 32px;
    transform: rotate(49deg);
    z-index: 1;
}

#leaves i:nth-of-type(n) {
    height: 23px;
    width: 30px;
}

#leaves i:nth-of-type(n):before {
    width: 7px;
    height: 5px;
    top: 17px;
    right: 1px;
}

#leaves i:nth-of-type(n):after {
    width: 2px;
    height: 17px;
    left: 12px;
    top: 0px;
}

#leaves i:nth-of-type(2n+1) {
    height: 11px;
    width: 16px;
}

#leaves i:nth-of-type(2n+1):before {
    width: 4px;
    height: 3px;
    top: 7px;
    right: 0px;
}

#leaves i:nth-of-type(2n+1):after {
    width: 2px;
    height: 6px;
    left: 5px;
    top: 1px;
}

#leaves i:nth-of-type(3n+2) {
    height: 17px;
    width: 23px;
}

#leaves i:nth-of-type(3n+2):before {
    height: 4px;
    width: 4px;
    top: 12px;
    right: 1px;
}

#leaves i:nth-of-type(3n+2):after {
    height: 10px;
    width: 2px;
    top: 1px;
    left: 8px;
}

#leaves i:nth-of-type(n) {
    animation-delay: 1.9s;
}

#leaves i:nth-of-type(2n) {
    animation-delay: 3.9s;
}

#leaves i:nth-of-type(3n) {
    animation-delay: 2.3s;
}

#leaves i:nth-of-type(4n) {
    animation-delay: 4.4s;
}

#leaves i:nth-of-type(5n) {
    animation-delay: 5s;
}

#leaves i:nth-of-type(6n) {
    animation-delay: 3.5s;
}

#leaves i:nth-of-type(7n) {
    animation-delay: 2.8s;
}

#leaves i:nth-of-type(8n) {
    animation-delay: 1.5s;
}

#leaves i:nth-of-type(9n) {
    animation-delay: 3.3s;
}

#leaves i:nth-of-type(10n) {
    animation-delay: 2.5s;
}

#leaves i:nth-of-type(11n) {
    animation-delay: 1.2s;
}

#leaves i:nth-of-type(12n) {
    animation-delay: 4.1s;
}

#leaves i:nth-of-type(13n) {
    animation-delay: 1s;
}

#leaves i:nth-of-type(14n) {
    animation-delay: 4.7s;
}

#leaves i:nth-of-type(15n) {
    animation-delay: 3s;
}

#leaves i:nth-of-type(n) {
    background: #6957BA;
}

#leaves i:nth-of-type(2n+2) {
    background: #6957BA;
}

#leaves i:nth-of-type(4n+1) {
    background: #6957BA;
}

#leaves i:nth-of-type(n) {
    opacity: 1;
}

#leaves i:nth-of-type(3n+1) {
    opacity: .8;
}

#leaves i:nth-of-type(3n+2) {
    opacity: .6;
}

#leaves i:nth-of-type(n) {
    transform: rotate(180deg);
}

#leaves i:nth-of-type(n) {
    animation-timing-function: ease-in-out;
}

@keyframes falling {
    0% {
        -webkit-transform: translate3d(300, 0, 0) rotate(0deg);
    }
    100% {
        -webkit-transform: translate3d(-350px, 700px, 0) rotate(90deg);
        opacity: 0;
    }
}

@keyframes falling3 {
    0% {
        -webkit-transform: translate3d(0, 0, 0) rotate(-20deg);
    }
    100% {
        -webkit-transform: translate3d(-230px, 640px, 0) rotate(-70deg);
        opacity: 0;
    }
}

@keyframes falling2 {
    0% {
        -webkit-transform: translate3d(0, 0, 0) rotate(90deg);
    }
    100% {
        -webkit-transform: translate3d(-400px, 680px, 0) rotate(0deg);
        opacity: 0;
    }
}

@media screen and (max-width:1799px) {
    .main-text {
        max-width: 590px;
        padding: 200px 0 45px 0;
    }
    .main-text h1 {
        font-size: 38px;
        line-height: inherit;
    }
}

@media screen and (max-width:1439px) {
    .mountain {
        height: auto;
    }
}

@media screen and (max-width:1279px) {
    .me {
        top: -130px;
        width: 200px;
    }
    .main-stars {
        height: auto;
    }
    .main-text {
        max-width: 415px;
    }
    .main-text h1 {
        font-size: 28px;
    }
}

@media screen and (max-width:1023px) {
    .me {
        width: 150px;
    }
    .mountain {
        left: 50%;
        transform: translateX(-50%);
        width: 150%;
    }
    .main-stars {
        left: 50%;
        transform: translateX(-50%);
        top: 125px;
        width: 150%;
    }
}

@media screen and (max-width:767px) {
    .mountain {
        width: 250%;
    }
    .main-text h1 {
        font-size: 22px;
    }
    .main-text {
        max-width: fit-content;
    }
    .main-stars {
        top: 100px;
        width: 250%;
    }
}