.up-wrapper {
    button {
        position: fixed;
        bottom: 46px;
        right: 26px;
        width: 75px;
        height: 75px;
        background: #fff;
        border-radius: 50%;
        opacity: 0;
        visibility: hidden;
        transition: 0.5s;
        border: none;
        transition: 0.5s;
    }
    button:hover {
        transform: scale(1.1);
    }
    button.show {
        opacity: 1;
        visibility: visible;
    }
    .up-img {
        width: 25px;
        object-fit: contain;
    }
}

@media screen and (max-width:1439px) {
    .up-wrapper .up-img {
        width: 15px;
    }
    .up-wrapper button {
        width: 50px;
        height: 50px;
    }
    .three-title {
        font-size: 32px;
    }
}

@media screen and(max-width:767px) {
    .up-wrapper {
        display: none;
    }
}