.header-wrapper {
    width: 100%;
    left: 50%;
    transform: translateX(-50%);
    padding-top: 45px;
    position: absolute;
}

.header-body {
    align-items: center;
    display: flex;
    max-width: 1650px;
    margin: 0 auto;
}

.header-wrapper.show {
    animation: fadeDown 0.5s ease;
    position: fixed;
    max-width: 100%;
    z-index: 9;
    background: #fff;
    padding-top: 20px;
    padding-bottom: 20px;
    box-shadow: 0 4px 100px rgba(105, 87, 186, 15);
    .header-nav li,
    .header-country a {
        color: #6957BA !important;
    }
    .header-logo path {
        fill: #6957BA !important;
    }
    .header-body {
        max-width: 1650px;
        margin: 0 auto;
    }
}

@keyframes fadeDown {
    0% {
        transform: translateX(-50%) translateY(-100%);
    }
    100% {
        transform: translateX(-50%) translateY(0%);
    }
}

.header-nav {
    font-size: 24px;
    line-height: 29px;
    width: 100%;
    max-width: 439px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-left: 260px;
    li {
        list-style-type: none;
        color: #FFFFFF;
    }
}

.header-country {
    font-size: 24px;
    line-height: 29px;
    margin-left: auto;
    a {
        text-decoration: none;
        color: #FFFFFF;
    }
}

.scroll-link {
    position: relative;
     ::before {
        content: "";
        position: absolute;
        width: 0%;
        height: 2px;
        background: #fff;
        transition: 0.5s;
        bottom: 0;
        left: 0;
    }
     :hover::before {
        width: 100%;
    }
}

.burger2 {
    display: none;
}

@media screen and (max-width:1799px) {
    .header-nav li {
        font-size: 20px;
    }
    .header-country {
        font-size: 20px;
        line-height: inherit;
    }
    .header-body {
        max-width: 1300px;
    }
    .header-wrapper.show .header-body {
        max-width: 1300px;
        margin: 0 auto;
    }
}

@media screen and (max-width:1439px) {
    .header-body {
        max-width: 1200px;
    }
    .header-wrapper.show .header-body {
        max-width: 1200px;
    }
}

@media screen and (max-width:1279px) {
    .header-body {
        max-width: 950px;
    }
    .header-wrapper.show .header-body {
        max-width: 950px;
    }
    .header-wrapper.show {
        padding-top: 10px;
        padding-bottom: 10px;
    }
    .header-wrapper {
        padding-top: 25px;
        z-index: 9;
    }
    .header-nav {
        position: fixed;
        top: 0;
        left: 0;
        width: 100vw;
        height: 100vh;
        max-width: 100%;
        margin: 0;
        flex-direction: column;
        padding: 20vw;
        z-index: 9;
        background: #fff;
        opacity: 0;
        visibility: hidden;
        transition: 0.5s;
    }
    .header-nav li {
        color: #6957BA !important;
    }
    .burger2.on+.header-nav {
        opacity: 1;
        visibility: visible;
    }
    &.burger2 {
        display: block;
        z-index: 10;
        width: 40px;
        height: 20px;
        position: relative;
        background: transparent;
        border: none;
        margin-left: auto;
        order: 1;
        &:after,
        &:before,
        span {
            width: 100%;
            height: 2px;
            border-radius: 100px;
            position: absolute;
            display: block;
            background: #fff;
        }
        &:after,
        &:before {
            content: "";
            transition: 0.3s width 0.4s;
        }
        &:after {
            top: 0px;
            left: 0px;
            margin-top: 0px;
        }
        &:before {
            bottom: 0px;
            right: 0px;
            margin-bottom: 0px;
        }
        span {
            top: 50%;
            margin-top: -1px;
            transition: transform 0.3s;
            &:before {
                content: "";
                background: #fff;
                width: 100%;
                height: 2px;
                border-radius: 100px;
                position: absolute;
                left: 0px;
                transition: transform 0.3s;
            }
        }
        &.on {
            &:after {
                transition: all 0.2s;
                width: 0%;
                left: 0px;
            }
            &:before {
                transition: all 0.2s;
                width: 0%;
                right: 0px;
            }
            span {
                transform: rotate(45deg);
                transition: 0.3s transform 0.4s;
                background: #6957BA;
                &:before {
                    transform: rotate(-90deg);
                    transition: 0.3s transform 0.4s;
                    background: #6957BA;
                }
            }
        }
    }
    .header-wrapper.show .burger2:before,
    .header-wrapper.show .burger2 span:before,
    .header-wrapper.show .burger2::after {
        background: #6957BA;
    }
    .header-country {
        display: none;
    }
}

@media screen and (max-width:1023px) {
    .header-body {
        max-width: 700px;
    }
}

@media screen and (max-width:767px) {
    .header-body {
        max-width: 89.3%;
    }
    .header-nav {
        padding: 60vw 20vw;
    }
    .header-wrapper.show .header-body {
        max-width: 89.3%;
    }
}