* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
    font-family: "Montserrat", sans-serif;
    background: linear-gradient(270deg, #6855EC 0%, #CA79DF 100%);
    overflow-x: hidden;
    max-width: 100vw;
    cursor: none;
}

h1,
h2,
h3 {
    color: rgb(54, 54, 54);
}

h3,
h4 {
    font-weight: 400;
    color: rgb(99, 99, 99);
}

.App {
    transition: all 0.5s ease;
    max-width: 1800px;
    margin: 0 auto;
}


/* width */

::-webkit-scrollbar {
    width: 8px;
    transition: 0.5s;
}


/* Track */

::-webkit-scrollbar-track {
    background: #fff;
    transition: 0.5s;
}


/* Handle */

::-webkit-scrollbar-thumb {
    background: linear-gradient(0deg, #6855EC 0%, #CA79DF 100%);
    border-radius: 8px;
    transition: 0.5s;
}

// /* Handle on hover */
// ::-webkit-scrollbar-thumb:hover {
//   background: linear-gradient(0deg, #6855EC 0%, #CA79DF 100%);
// }
#root {
    position: relative;
    overflow-x: hidden;
    overflow-y: clip;
}

@media screen and (max-width:1799px) {
    .App {
        max-width: 1300px;
    }
}

@media screen and (max-width:1439px) {
    .App {
        max-width: 1200px;
    }
}

@media screen and (max-width:1279px) {
    .App {
        max-width: 950px;
    }
}

@media screen and (max-width:1023px) {}

@media screen and (max-width:767px) {}

